.footer__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px;
  height: 20px;
  text-align: center;
  font-size: 15px;
  border-top: 1px solid #e6e6e6;
  color: gray;
}
@media (max-width: 430px) {
  .footer__container {
    height: 50px;
  }
}
.footer__container:before {
  content: "";
  position: absolute;
  z-index: -1;
  right: 0;
  bottom: 0;
  left: 0;
  height: 50px;
  overflow: hidden;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2), 0 8px 0 -3px #f6f6f6, 0 9px 1px -3px rgba(0, 0, 0, 0.2), 0 16px 0 -6px #f6f6f6, 0 17px 2px -6px rgba(0, 0, 0, 0.2);
}

.footer__todo-count {
  text-align: left;
}

.footer__clear_completed {
  position: relative;
  line-height: 20px;
  text-decoration: none;
  cursor: pointer;
}
.footer__clear_completed:hover {
  text-decoration: underline;
}

